:import('../../colors.css'){
  lightBlue: lightBlue;
  blue: blue;
}

:root {
  --side-panel-width: 280px;
}

.Container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-line-pack: center;
      align-content: center;
  width: 80%;
  height: calc(100vh - 50px);
  -ms-flex-item-align: center;
      align-self: center;
  border-top: 1px solid #75b7f6;
  padding: 0 20px 20px 20px;
  max-width: 1400px;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}

.BreadCrumbs {
  margin-bottom: 20px;
  width: 100%;
  /* min-width: 1000px; */
  -ms-flex-item-align: start;
      align-self: flex-start;
}

.Main {
  width: 100%;
  height: 100%;
  -ms-flex-positive: 2;
      flex-grow: 2;
  display: -ms-flexbox;
  display: flex;
}


.SidePanel {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-right: 30px;
  padding-right: 30px;
  width: var(--side-panel-width);
  border-right: 1px solid #ddd;
  letter-spacing: 0.06em;
}


.Tabs {
  /* border: 1px solid orange; */
}

.Image {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 250px;
  width: 250px;
  border-radius: 7px;
  background-color: #f3f3f3;
  border: 1px solid #ddd;
}

.Details {
  margin-top: 5px;
}

.CourseName {
  font-size: 1.2em;
}

.Content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  width: calc(100% - var(--side-panel-width))
  /* flex-grow: 3; */
  /* max-width: 850px; */
  /* border: 2px solid pink; */
}

.MainContent {
  /* border: 1px solid blue; */
  padding-top: 30px;
}


.Trash {
  position: absolute;
  bottom: 30px;
  right: 30px;
  font-size: 3em;
}

.CreateContainer {

}

.spMain {
  font-size: 25px;
  font-weight: 900;
}

.spSecondary {
  font-size: 20px;
  color: #666;
}

.spAdditional {
  padding: 3px 0; 
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}

.Edit {
  color: #999;
  cursor: pointer;
}

.Edit:hover {
  color: blue;
}

.CreateForParticipant {
  width: 100%;
  text-align: center;
  font-style: italic;
  color: #666;
  font-size: 15px;
  cursor: pointer;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}

.CreateForParticipant:hover {
  color: lightBlue;
}

@media screen and (max-width: 1100px) {
  .Container {
    -ms-flex-item-align: start;
        align-self: flex-start
  }
}


