:import('../../../colors.css') {
  blue: blue;
  lightBlue: lightBlue;
  yellow: yellow;
  teal: teal;
  lightShadow: lightShadow;
  backgroundGray: backgroundGray;
}

.CrumbContainer {
  z-index: 500;
  display: -ms-flexbox;
  display: flex;
  background-color: #e9e9e9;
  /* border: 2px solid #666 inset; */
  padding: 0 40px 0 4px;
  border-radius: 0 0 14px 14px;
  height: 27px;
  -webkit-box-shadow: lightShadow;
          box-shadow: lightShadow;
  /* width: auto; */
}

.Crumb {
  margin-bottom: 20px;
  color: white;
  background-color:lightBlue;
  padding: 3px;
  border-radius: 0 0 10px 10px;
  text-align: center;
  height: 90%;
  margin-right: -10px;
  width: 200px;
  border: 2px solid white;
  border-top: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  /* justify-content: center; */
  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: '-';
     text-overflow: '-';
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}

.Active {
  color: yellow;
  /* border: 2px solid yellow; */
  border-top: none;
}

@media screen and (max-width: 800px) {
  .Crumb {
    width: 100px;
    text-align: left;
  }
}


