:import('../../colors.css'){
  lightestShadow: lightestShadow;
  blue: blue;
  lightBlue: lightBlue;
}

.Search {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  background: #f9f9f9;
  /* border: 2px solid #ddd; */
  /* border-radius: 5px; */
  font-size: inherit;
  height: 100%;
  width: 100%;
  -webkit-box-shadow: lightestShadow;
          box-shadow: lightestShadow;
}

.Input {
  border: none;
  border-bottom: 1px solid #f9f9f9;
  width: 100%;
  padding: 5px;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  background-color: #f9f9f9;
  font-size: inherit;
  letter-spacing: .08em;
  color: #666;
  caret-color: lightBlue;
}
.Input:focus{
  border: none;
  border-bottom: 1px solid blue;
  outline-width: 0;
}

.Icon {
  font-size: inherit;
  padding: 5px;
}



