:import('../../colors.css'){
  gray: backgroundGray;
  blue: blue;
  red: red;
  lightShadow: lightShadow;
}


.Container {
  width: 100%;
  /* height: 100%; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.ModalContainer{

}

.SignupContainer {
  /* background: gray; */
  margin-top: 40px;
  border-radius: 4px;
  padding: 15px;
  width: 66%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  /* box-shadow: lightShadow; */
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  z-index: 300;
  color: white;
}

.Form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-flow: column;
      flex-flow: column;
}

.Title {
  /* color: blue; */
  letter-spacing: .09em;
}

.Form > div {
  width: 90%;
}

.Radios {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100%;
  /* margin: 10px; */
}

.ErrorMsg {
  position: relative;
  color: red;
  height: 30px;
  padding: 10px;
  width: 100%;
  height: 20px;
}

.Error {
  position: absolute;
  width: 100%;
  text-align: center;
}

.Submit {
  margin: 20px 0;
}



