.Container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  background-color: white;
  border: 1px solid #ddd;
}

.Avatar {
  margin: 20px;
  /* width: 100px; */
}

.Notification {
  background: red;
  color: white;
  padding: 3px 10px;
  border-radius: 10px;
}

.Row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  /* width: 150px; */
}

.Role {
  margin: 20px;
}

.Edit {
  /* padding: 5px; */
  margin-right: 20px;
  cursor: pointer;
  font-size: 19px;
  color: black;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}

.Edit:hover {
  color: #888;
}

.Trash {
  font-size: 23px;
  color: black;
  /* padding: 5px;s */
  margin-right: 20px;
  cursor: pointer;
}

.Trash:hover {
  color: #888;
}

.DropDown {
  /* width: 100px; */
  margin-right: 20px;
}

@media screen and (max-width: 800px) {
  .Trash, .Edit {
    /* justify-content: flex-start; */
    margin-right: 5px;
  }
  .Row {
    
    /* width: 100px; */
  }
}


