.Item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-preferred-size: 100px;
      flex-basis: 100px;
  list-style-type: none;
  height: 100%;
  -webkit-transition: .2;
  -o-transition: .2;
  transition: .2;
  position: relative;
  /* border: 1px solid green; */
}

.Link {
  padding: 9px;
  /* color: white; */
  letter-spacing: .07em;
}

/* .Item:hover {
  color: white;
  background-color: #0078EC;
  text-decoration: none;
}

.Link:active{
  color: white;
  background-color: #0078EC;
} */

.Item:after{
  content: '';
  position: absolute;
  width: 0; height: 2px;
  display: block;
  border-radius: 4px;
  margin-top: 25px;
  right: 0;
  background: #ddd;
  -webkit-transition: width .2s ease-in;
  -o-transition: width .2s ease-in;
  transition: width .2s ease-in;
  /* -webkit-transition: width .2s; */
}
 
.Item:hover:after{
  width: 100%;
  left: 0;
  background: #ddd;
}



