:import('../../../colors.css'){
  lightBlue: lightBlue;
}

.Link {
  color: lightBlue;
  padding-bottom: 2px;
  border-bottom: none;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}

.Link:hover {
  border-bottom: 1px solid #999;
}


