:import('../../../colors.css') {
  darkBlue: darkBlue;
  blue: blue;
  darkShadow: darkShadow;
  lightShadow: lightShadow;
  navbarHeight: navbarHeight
}

.Nav {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: navbarHeight;
  /* background-image: linear-gradient(to top, #3a5ec9 10%, #001144 100%); */
  -ms-flex-pack: center;
      justify-content: center;
  /* border-radius: 3px; */
  /* box-shadow: darkShadow; */
  z-index: 1000;
  border-top: 2px solid darkBlue;
  position: fixed;
  color: white;
  background: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  -webkit-transition: color 1s, background .5s, -webkit-box-shadow .5s;
  transition: color 1s, background .5s, -webkit-box-shadow .5s;
  -o-transition: color 1s, background .5s, box-shadow .5s;
  transition: color 1s, background .5s, box-shadow .5s;
  transition: color 1s, background .5s, box-shadow .5s, -webkit-box-shadow .5s;
}

.LightNav {
  color: blue;
  border-bottom: 1px solid #999;
  background: rgb(255, 255, 255);
  -webkit-box-shadow: lightShadow;
          box-shadow: lightShadow;

}

.NavContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  height: 100%;
  width: 1300px;
  margin: 0 20px;
}

.NavList{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  width: 50%;
}

.Logo {
  font-size: 30px;
  /* color: white; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  letter-spacing: .07em;
}



