:import('../../../colors.css') {
  blue: blue;
  orange: orange;
  lightestShadow: lightestShadow;
  lightShadow: lightShadow;
}

.Button { 
  /* background-color: white; */
  border: none;
  -ms-flex-item-align: center;
      align-self: center;
  padding: 8px;
  border-radius: 3px;
  /* border: 1px solid #d3d2d4; */
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  font-size: inherit;
  letter-spacing: .1em;
  cursor: pointer;
  /* box-shadow: lightestShadow; */
}

.Big {
  color: white;
  padding: 25px;
  background: none;
  border: 2px solid white;
  border-radius: 0px;
  /* box-shadow: 0; */
}

.Big:hover {
  -webkit-box-shadow: lightShadow;
          box-shadow: lightShadow;
  background-color: blue;
  border: 2px solid blue;
  border-radius: 3px;
}

.Small {
  background-color: blue;
  color: white;
  -webkit-box-shadow: lightestShadow;
          box-shadow: lightestShadow;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}


.SmallCancel {
  background-color: #ccc;
  color: #ddd;
}
.Small:hover {
  -webkit-box-shadow: lightShadow;
          box-shadow: lightShadow;
}
.Primary {
}

.Secondary {
  color: white;
  background-color: orange;
}

.Active {
  color: white;
  background-color: blue;
}

.Primary:hover {
  /* color: white; */
  background-color: #f9f9f9;
  border: 1px solid #babbb2;
}

.Active:hover {
  color: white;
  background-color: blue;
}

.Secondary:hover {
  color: white;
  -webkit-box-shadow: lightShadow;
          box-shadow: lightShadow;
}



